.roadmap {
	padding-top: 296rem;

	@media (max-width: 991px) {
		padding-top: 150rem;
	}

	&__bg {
		margin-top: 80rem;
		padding-top: 200rem;
		background-image: url('../img/roadmap-bg.png');
		background-size: 100% 100%;
		background-position: center;
		background-repeat: no-repeat;
		height: 2000rem;

		@media (max-width: 991px) {
			height: auto;
			padding-top: 100rem;
			margin-top: 40rem;
		}
	}

	&__start {
		font-weight: 700;
		font-size: 28rem;
		line-height: 37rem;
		color: #fff;
	}

	&__content {
		margin-top: 11rem;
	}

	&__item {
		position: relative;

		&::before {
			content: '';
			position: absolute;
			top: 0;
			width: 58rem;
			height: 567rem;
			border-radius: 50px;

			@media (max-width: 991px) {
				content: none;
			}
		}

		&:nth-child(1) {
			.roadmap__months {
				background: #72F596;
			}

			&:before {
				background: #72F596;
				right: 0;
			}
		}

		&:nth-child(2) {
			.roadmap__months {
				background: #D1D3D4;

				&:before {
					content: '';
					position: absolute;
					top: 0;
					right: 0;
					width: 1275rem;
					height: 58rem;
					border-radius: 50px;
					background: #72F596;

					@media (max-width: 991px) {
						content: none;
					}
				}
			}

			&:before {
				background: #D1D3D4;
				left: 0;
			}
		}

		&:nth-child(3) {
			.roadmap__months {
				background: #D1D3D4;
			}
		}
	}

	&__months {
		position: relative;
		display: flex;
		justify-content: space-around;
		border-radius: 50rem;
	}

	&__month {
		position: relative;
		z-index: 10;
		font-weight: 700;
		font-size: 28rem;
		line-height: 58rem;

		@media (max-width: 991px) {
			font-size: 22rem;
			padding: 10rem 0;
		}
	}

	&__lists {
		display: flex;
		justify-content: space-around;
		padding: 30rem 0;

		@media (max-width: 991px) {
			display: grid;
			grid-template-columns: repeat(3, 1fr);
			grid-gap: 10rem;
		}
	}

	&__list {
		background: #fff;
		box-shadow: 0px 2px 14px 4px rgba(0, 0, 0, 0.25);
		border-radius: 30rem;
		padding: 18rem;
		max-width: 345rem;
		min-height: 400rem;

		@media (max-width: 991px) {
			padding: 18rem 13rem;
			min-height: auto;
		}

		li {
			font-size: 16rem;
			line-height: 21rem;
			text-transform: uppercase;
		}

		li+li {
			margin-top: 20rem;
		}
	}
}