.footer {
	background-image: url('../img/footer-bg.png');
	background-size: 100% 100%;
	background-position: center;
	background-repeat: no-repeat;
	height: 500rem;
	margin-top: -220rem;

	@media (max-width: 991px) {
		background-size: cover;
	}

	&__content {
		padding-top: 200rem;
		display: flex;
		flex-direction: column;
		align-items: center;

		@media (max-width: 991px) {
			padding-top: 170rem;
		}
	}

	&__socials {
		display: flex;

		a {
			display: block;
			width: 50rem;
			height: 50rem;

			img {
				width: 100%;
				height: 100%;
				object-fit: contain;
			}

			&:hover {
				opacity: 0.6;
			}
		}

		a+a {
			margin-left: 100rem;

			@media (max-width: 991px) {
				margin-left: 50rem;
			}
		}
	}

	&__mail {
		padding-top: 60rem;
	}

	&__link {
		font-weight: 600;
		font-size: 18rem;
		color: #fff;
		padding-left: 55rem;
		background-position: left;
		background-repeat: no-repeat;
		background-image: url('../img/svg/mail.svg');
		background-size: 46rem;

		&:hover {
			opacity: 0.6;
			color: #fff;
		}
	}

	&__copy {
		padding: 60rem 15rem 0 15rem;
		font-weight: 600;
		font-size: 18rem;
		line-height: 26rem;
		color: #fff;
		text-transform: uppercase;

		@media (max-width: 991px) {
			text-align: center;
		}
	}
}