.comingsoon {
	padding-top: 100rem;

	&__items {
		display: flex;
		justify-content: space-around;
		margin-top: 65rem;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__item+&__item {
		@media (max-width: 991px) {
			margin-top: 70rem;
		}
	}

	&__watch {
		width: 350rem;
		height: 390rem;
		margin: auto;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__link {
		display: block;
		width: 370rem;
		height: 109rem;
		margin: 40rem auto 0;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}