.header {
	position: absolute;
	top: 20rem;
	left: 0;
	width: 100%;
	padding: 0 25rem;

	@media (max-width: 991px) {
		top: 10rem;
		padding: 0 10rem;
	}

	&__content {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__logo {
		width: 60rem;
		height: 46rem;

		@media (max-width: 991px) {
			width: 35rem;
			height: 35rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__buttons {
		display: flex;

		@media (min-width: 992px) {
			padding-left: 105rem;
		}
	}

	&__button {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 46rem;
		position: relative;
		box-shadow: 0px 3px 4px 1px rgba(0, 0, 0, 0.25);
		border-radius: 30rem;
		font-weight: 700;
		font-size: 24rem;
		color: #fff;
		transition: all 0.3s ease;

		@media (max-width: 991px) {
			font-size: 16rem;
			height: 35rem;
		}

		&--presale {
			background-color: #35DD40;
			padding: 10rem 20rem 10rem 50rem;

			&:hover {
				background-color: #2ebe37;
				color: #fff;
			}

			&::before {
				background-image: url('../img/svg/shop.svg');
				width: 25rem;
				height: 17rem;
				left: 20rem;
			}
		}

		&--telegram {
			background-color: #3578DD;
			padding: 10rem 20rem 10rem 50rem;

			&:hover {
				background-color: #2d68c0;
				color: #fff;
			}

			&::before {
				background-image: url('../img/svg/telegram.svg');
				width: 20rem;
				height: 18rem;
				left: 20rem;
			}
		}

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
		}
	}

	&__button+&__button {
		margin-left: 20rem;

		@media (max-width: 991px) {
			margin-left: 10rem;
		}
	}

	&__whitepaper {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 46rem;
		padding: 10rem 20rem;
		background: #fff;
		border: 1px solid #000;
		border-radius: 30rem;
		font-family: 'PumpDemiBoldLetPlain', sans-serif;
		font-size: 24rem;
		color: #000;

		@media (max-width: 991px) {
			font-size: 16rem;
			height: 35rem;
		}
	}
}