.features {
	padding-top: 47rem;

	&__text {
		max-width: 700rem;
		margin: 30rem auto 0;
		font-weight: 600;
		font-size: 26rem;
		line-height: 35rem;
		text-align: center;
		text-transform: uppercase;

		p+p {
			margin-top: 40rem;

			@media (max-width: 991px) {
				margin-top: 20rem;
			}
		}

		@media (max-width: 991px) {
			font-size: 28rem;
			line-height: 37rem;
		}
	}

	&__content {
		margin-top: 138rem;
		display: flex;
		align-items: center;

		@media (max-width: 991px) {
			margin-top: 60rem;
			flex-direction: column;
		}
	}

	&__img {
		width: 584rem;
		height: 445rem;

		@media (max-width: 991px) {
			width: 384rem;
			height: 295rem;
			margin-bottom: 40rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__items {
		@media (min-width: 992px) {
			margin-left: 23rem;
			padding-bottom: 20rem;
		}
	}

	&__item {
		display: flex;
		align-items: center;
	}

	&__item+&__item {
		margin-top: 48rem;
	}

	&__field {
		background: #000;
		border-radius: 30rem;
		padding: 3rem 22rem;
		min-width: 200rem;
		line-height: 35rem;
		font-size: 22rem;
		color: #fff;
	}

	&__desc {
		margin-left: 20rem;
		font-size: 22rem;
		line-height: 28rem;
		text-transform: uppercase;
	}
}