.intro {
	padding-top: 150rem;
	background-image: url('../img/intro-bg.png');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	height: 1184rem;

	@media (max-width: 991px) {
		height: 400rem;
		padding-top: 80rem;
	}

	&__logo {
		width: 389rem;
		height: 98rem;
		margin: auto;

		@media (max-width: 991px) {
			width: 229rem;
			height: 58rem;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}