.video {
	padding-top: 204rem;

	&__bg {
		padding: 100rem 0 300rem;
		background-image: url('../img/video-bg.png');
		background-size: 100% 100%;
		background-position: center;
		background-repeat: no-repeat;
	}

	&__items {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 50rem;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&__item {
		text-align: center;
	}

	&__video {
		position: relative;
		width: 430rem;
		height: 510rem;
		margin: auto;

		@media (max-width: 991px) {
			width: 100%;
			height: 300rem;
		}

		video {
			width: 100%;
			height: 100%;
			object-fit: cover;
			cursor: pointer;
			border-radius: 13rem;
		}
	}

	&__name {
		margin-top: 35rem;
		font-family: 'PumpDemiBoldLetPlain', sans-serif;
		font-size: 38rem;
		line-height: 1;

		@media (max-width: 991px) {
			font-size: 28rem;
		}
	}

	&__desc {
		margin-top: 10rem;
		font-size: 22rem;
		line-height: 28rem;

		@media (max-width: 991px) {
			font-size: 18rem;
		}
	}
}